'use strict';
var React = require('react/addons');
var Modal=require('react-bootstrap/lib/Modal');
var Button=require('react-bootstrap/lib/Button');
var Reflux = require('reflux');
var Actions=require('../../actions/saveActions.js');
var CheckBox=require('../commons/customCheckbox.jsx');
var API=require('../../api/saveAndRestore.js');
var _=require('lodash');
var tim = require('tinytim').tim;
var Loading=require('../commons/loading.jsx');
var LanStore=require('../../stores/lanStore.js');


module.exports = React.createClass({displayName: "exports",

	mixins: [Reflux.connect(LanStore, 'lan')],

	_close:function(){
		this.setState({'visible':false});
	},

	_open:function(){
		this.setState({'visible':true, 'file':null});
		this.forceUpdate();
		this._initDownload();
	},

	getInitialState:function(){
		return {'visible':false, 'file':null}; 
	},

	_initDownload:function(){
		API.pdf(this.props.id).then(function(data){
			this.setState({'visible':true, 'file':data.name});
			this.forceUpdate();
		}.bind(this));
	},

	render:function() {
		var downloadMessage=(
			React.createElement("p", {className: "bs-callout bs-callout-success"}, 
				React.createElement("a", {href: tim(window.MAP_DOWNLOAD_URL,{'name':this.state.file||null})}, React.createElement(Message, {message: "savemap.printreadymessage"}))
			)
			);
		var waitMessage=(
			React.createElement("p", {className: "bs-callout bs-callout-warning"}, 
				React.createElement("img", {src: "images/ajax-loader.gif", width: "20", height: "20"}), 
				React.createElement(Message, {message: "savemap.printloadmessage"})
			)
			);
		return (
			React.createElement("span", null, 
				React.createElement("a", {href: "#"}, 
					React.createElement("i", {className: "pull-right fa fa-file-pdf-o", title: i18n.t('savemap.tooltipprint'), onClick: this._open})
				), 
				React.createElement(Modal, {className: "dialog-print-map", bsSize: "large", "aria-labelledby": "contained-modal-title-lg", show: this.state.visible, onHide: this.close}, 
					React.createElement(Modal.Header, null, 
						React.createElement(Modal.Title, null, 
							React.createElement("i", {className: "fa fa-folder-open"}), " ", React.createElement(Message, {message: "savemap.downloadtitle"})
						), 
						React.createElement("a", {className: "close-dialog", href: "#", onClick: this._close}, 
							React.createElement("i", {className: "fa fa-times-circle-o"})
						)
					), 
					React.createElement(Modal.Body, null, 
						(this.state.file!=null)?downloadMessage:waitMessage
					)
				)
			)
		);
	}
});
