'use strict';
var React = require('react');
var Reflux = require('reflux');
var _=require('lodash');
var CommonsMixins=require('./commonsMixins.jsx');
var Item=require('./singleListItem.jsx');


module.exports = React.createClass({displayName: "exports",
   mixins: [CommonsMixins],

  getInitialState: function() {
    return {
      items: [],
      selected: [],
    };
  },

  render: function() {
    console.log(this.state);
    var items=_.filter(this.state.items, function(item){return item.selected;});
    if(items.length > 0){
      return (
        React.createElement("div", {className: "filter-list-container"}, 
          React.createElement("h4", null, React.createElement(Message, {message: this.props.label})), 
          React.createElement("ul", {className: "filter-list"}, 
          
            _.map(items,function(item) {
              return (React.createElement("li", null, React.createElement(Item, React.__spread({},  item))))                    
            },this) 
          
          )
        )
      );
    } else {
      return null;
    }
  }
});



