'use strict';

var React = require('react');
var Reflux = require('Reflux');

var ArcgisLoginStore = require('../../stores/arcgisLoginStore.js');

module.exports =React.createClass({displayName: "exports",

	mixins: [Reflux.connect(ArcgisLoginStore, 'login')],
	
	render:function(){		
		var html;
		if (this.state.login.profile &&  this.state.login.profile.user){
			html=(React.createElement("a", {href: "#"}, this.state.login.profile.user.username, " ", React.createElement("span", null, "|")) );
		} else {
			html=(React.createElement("p", null))
		}
		return html;
	}
});