'use strict';

var React = require('react/addons');
var Reflux = require('reflux');
var _ = require('lodash');
var ColorPicker=require('./_colorPicker.jsx');

module.exports = React.createClass({displayName: "exports",

  _changeColor: function(color) {
    this.props.onChangeColor(color, this.props.level);
  },

  _changeRadius: function(event) {    
    this.setState({'radius': event.currentTarget.value});
    this.props.onChageRadius(event.currentTarget.value, this.props.level);
  },

  getInitialState: function() {
    return {'radius': (this.props.radius || 0)};
  },
  
  render:function(){
    return ( 
      React.createElement("div", {className: "breaksOptions"}, 
        React.createElement("div", {className: "label label-info"}, this.props.label), 
        React.createElement(ColorPicker, {level: this.props.level, onChangeColor: this._changeColor, color: this.props.color}), 
        this.props.onChageRadius?
          React.createElement("input", {type: "text", name: "radius", value: this.state.radius, onChange: this._changeRadius})
        :null, 
        React.createElement("div", {className: "clearFix"})
      )
    )
  }
});
